a:link {
  transition: all 0.3s ease;
}
.ac-btn {
  background: $ac-yellow;
  background-image: -webkit-linear-gradient(top, $ac-yellow, $ac-orange);
  background-image: -moz-linear-gradient(top, $ac-yellow, $ac-orange);
  background-image: -ms-linear-gradient(top, $ac-yellow, $ac-orange);
  background-image: -o-linear-gradient(top, $ac-yellow, $ac-orange);
  background-image: linear-gradient(to bottom, $ac-yellow, $ac-orange);
  -webkit-border-radius: 4;
  -moz-border-radius: 4;
  border-radius: 4px;
  color: $ac-text;
  padding: 16px 40px 18px 40px;
  border: solid $ac-brown 1px;
  text-decoration: none;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 30px;
  cursor: pointer;
}
.ac-btn:visited {
  color: $ac-text;
}

.ac-btn:hover {
  color: $ac-text;
  background: $ac-orange;
  background-image: -webkit-linear-gradient(top, darken($ac-yellow,20%), darken($ac-orange, 5%));
  background-image: -moz-linear-gradient(top, darken($ac-yellow,20%), darken($ac-orange, 5%));
  background-image: -ms-linear-gradient(top, darken($ac-yellow,20%), darken($ac-orange, 5%));
  background-image: -o-linear-gradient(top, darken($ac-yellow,20%), darken($ac-orange, 5%));
  background-image: linear-gradient(to bottom, darken($ac-yellow,20%), darken($ac-orange, 5%));
  text-decoration: none;
}
button.ac-btn {
  padding: 12px 40px 26px 40px;
  cursor: pointer;
  @include breakpoint(large) {
    padding: 10px 40px 28px 40px;
  }
}
a.ac-btn-phone-hero {
  border: solid 1px $ac-link;
  border-radius: 4px;
  padding: 4px 0 4px 0;
  display: flex;
  justify-content: center;
  color:$ac-link;
  background-color: $ac-light-blue;
  @include breakpoint(large) {
    padding: 2px 0 2px 0;
  }
  img {
    //margin-top: 0px;
    //margin-bottom:0px;
    width: 18px;
    margin-right: 6px;
  }
  &:hover {
    background-color: darken($ac-light-blue, 10%);
  }
}